import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { SigninDto } from 'src/app/shared/dtos/auth/signin-dto';
import { UserSigninService } from 'src/app/shared/services/signin.service';

@Component({
  selector: 'app-auth-signin',
  templateUrl: './auth-signin.component.html',
  styleUrls: ['./auth-signin.component.scss']
})
export class AuthSigninComponent extends AppComponentBase {

  user: SigninDto=new SigninDto();
  showPassword:boolean;
  saving=false;

  constructor(
      injector: Injector,
      private _router:Router,
      private _userSigninService:UserSigninService
    ) { 
      super(injector)
    }

  ngOnInit() {
    this.showPassword=false;
  }

  signin():void{debugger
    this.saving=true;
    this._userSigninService.SigninUser("/auth/signin",this.user).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
        if(response.status === 1){
          let result = response.data;
          localStorage.clear(); 
          localStorage.setItem("token",result.accessToken);
          localStorage.setItem("user",JSON.stringify(result.user));
          this._router.navigate(['/']);
        }else{
          this.notify.error("UserName or Password Incorrect","Error");
        }
    })
  }

}
